import zodToJsonSchema from "zod-to-json-schema";
import { DefaultService, OpenAPI, ApiError } from "./client/";
import type { ApiRequestOptions } from "./client/core/ApiRequestOptions";
import { getHeaders, getRequestBody } from "./client/core/request";
import type { MakeOptionalIfNull } from "./utils";
import type z from "zod";

let authorizePromise: Promise<any> | null = null;

type Endpoint = keyof typeof DefaultService;
type Method = (...args: any) => any;
type Arg<T extends Method> = Parameters<T>[0];
type WithBody<TBody = any> = { requestBody: TBody };

type RequestBodyKey = "requestBody";
type FormDataKey = "formData";
type UsesRequestBodyType = "body";
type UsesFormDataType = "form";
type UseQueryParamsType = "query";

type RequestType = UsesRequestBodyType | UseQueryParamsType | UsesFormDataType;

type ErrorHandler = { onError: (error: ApiError) => any; }

type OptionalErrorHandler = Partial<ErrorHandler>;


export type APIRequestBody<Key extends keyof typeof DefaultService> =
    (typeof DefaultService)[Key] extends Method ?
    RequestBodyKey extends keyof Parameters<(typeof DefaultService)[Key]>[0] ?
    [type: UsesRequestBodyType, data: MakeOptionalIfNull<Parameters<(typeof DefaultService)[Key]>[0][RequestBodyKey]> & OptionalErrorHandler] :
    FormDataKey extends keyof Parameters<(typeof DefaultService)[Key]>[0] ?
    [type: UsesFormDataType, data: MakeOptionalIfNull<Parameters<(typeof DefaultService)[Key]>[0][FormDataKey]> & OptionalErrorHandler] :
    [type: UseQueryParamsType, data: MakeOptionalIfNull<Parameters<(typeof DefaultService)[Key]>[0]> & OptionalErrorHandler] :
    [error: "Non-method / service key provided"];

//type X<Key> = Service<Key> extends never ? never : RequestBodyKey extends keyof Service<Key> ? 

type MaybeNullable<T, Optional extends boolean> = Optional extends true ? T | null : T;

export type APIResponse<Key extends keyof typeof DefaultService, AwaitedOrNot extends "promise" | "awaited" | "cancellable" = "awaited", Nullable extends boolean = false> =
    (typeof DefaultService)[Key] extends Method
    ? AwaitedOrNot extends "awaited"
    ? MaybeNullable<Awaited<ReturnType<(typeof DefaultService)[Key]>>, Nullable>
    : AwaitedOrNot extends "promise"
    ? Promise<MaybeNullable<Awaited<ReturnType<(typeof DefaultService)[Key]>>, Nullable>>
    : ReturnType<(typeof DefaultService)[Key]> : "Invalid key";

export function request<
    Key extends Endpoint,
    Entry = (typeof DefaultService)[Key],
    // Update to handle queries with bodies 
    NoBody extends boolean = Entry extends Method ? Arg<Entry> extends WithBody ? false : keyof Arg<Entry> extends "autoconnect" ? true : false : false
>(key: NoBody extends true ? Key : never): APIResponse<Key, "cancellable">;

export function request<Key extends Endpoint, Payload>(
    key: Key, ...details: APIRequestBody<Key> & [RequestType, Payload]
): Payload extends ErrorHandler
    ? APIResponse<Key, "cancellable"> | ReturnType<Payload["onError"]>
    : APIResponse<Key, "cancellable">;

export function request<Key extends Endpoint, Payload>(key: Key, ...details: ((APIRequestBody<Key> & [RequestType, Payload]) | [])): APIResponse<Key, "cancellable"> {
    const method = DefaultService[key] as Method;

    const service = authorizePromise
        /** If re-authorizing, allow promise to complete so the request is made with a valid access token */
        ? (payload: APIRequestBody<Key>[1]) => authorizePromise?.then(_ => method(payload))
        : method;

    if (details.length === 0) return service({});

    const [type, data] = details as APIRequestBody<Key>;
    const payload = type === "body" ? { requestBody: data } : type === "form" ? { formData: data } : type === "query" ? data : Error("Invalid request type") as APIRequestBody<Key>[1];

    const { onError } = data as OptionalErrorHandler;

    return onError
        ? (service(payload) as Promise<any>)
            .catch((error: unknown) => error instanceof ApiError ? onError(error) : error)
        : service(payload);
}

const reauthorizeBufferTimeMilliseconds = 1000;

let stopAutoRefresh = true;

export const authorize = async () => {
    OpenAPI.CREDENTIALS = "include"; // make configured based on dev?
    OpenAPI.WITH_CREDENTIALS = true;

    const response = await request("authToken", "query", {
        onError: _ => false as const
    });

    if (!response) return false;

    OpenAPI.TOKEN = response.access_token;

    const milliseconds = response.expiration_milliseconds;
    const reAuthDelay = milliseconds - reauthorizeBufferTimeMilliseconds;
    stopAutoRefresh = false;

    setTimeout(() => {
        if (stopAutoRefresh) return;
        authorizePromise = authorize();
    }, reAuthDelay);

    return true;
}

export const unauthorize = () => {
    stopAutoRefresh = true;
    OpenAPI.TOKEN = undefined;
}

// TODO: Use the key to determine the url / method after migrating to: https://github.com/hey-api/openapi-ts
export const stream = async <Key extends Endpoint>(
    key: Key, ...details: [...APIRequestBody<Key>, url: string]
): Promise<ReadableStreamDefaultReader<Uint8Array>> => {
    const [type, data, urlSuffix] = details;
    const url = OpenAPI.BASE + urlSuffix;
    const method = type === "body" ? "POST" : "GET";
    const options: ApiRequestOptions = { method, body: data, url };

    if (authorizePromise) await authorizePromise;

    const body = getRequestBody(options);
    const headers = await getHeaders(OpenAPI, options);

    const response = await fetch(url, {
        method: options.method,
        headers,
        body,
    });

    const { body: readableStream } = response;

    if (!readableStream) throw new Error("No readable stream body");

    return readableStream.getReader();
}

export function extractAndRemoveTags<T extends string>(text: string, ...tagNames: T[]) {
    let remainingText = text;
    const extracted = {} as Record<T, string[]>;

    for (const tagName of tagNames) {
        const regex = new RegExp(`(<${tagName}>.*?<\\/${tagName}>)`, 'gs');
        extracted[tagName] = [];
        let match;

        while ((match = regex.exec(remainingText)) !== null) {
            const fullMatch = match[1];
            // Extract content inside the tag
            const contentMatch = fullMatch.match(new RegExp(`<${tagName}>(.*?)<\/${tagName}>`, 's'));
            if (contentMatch) {
                extracted[tagName].push(contentMatch[1]);
            }
        }

        // Remove the matched tags from the text
        remainingText = remainingText.replace(regex, '');
    }

    return { extracted, remainingText };
}

export const tryExtractTag = (
    text: string,
    key: string,
): [text: string] | [updatedText: string, extracted: string] => {
    const { extracted, remainingText } = extractAndRemoveTags(text, key);
    const result = extracted[key];
    if (result.length === 0) return [text];
    if (result.length > 1)
        console.error(`Expected 1 ${key} tags, got ${result.length}`);
    return [remainingText, result[0]];
};

type Structurable = ReturnType<typeof z.object>;

export const zodToSchema = <Structure extends Structurable>(structure: Structure, name: string) => {
    const schema = zodToJsonSchema(structure, { name, });

    // Some hacks to make the schema -> pydantic model generator (on the backend) happy
    const obj = { ...schema.definitions![name] };
    (obj as any)["title"] = name;
    (obj as any)["$schema"] = "http://json-schema.org/draft-07/schema#";

    return JSON.stringify(obj);
}

export const parseStructuredStream = <T extends Structurable>(text: string) => {
    const multipleObjects = text.includes("}{");
    const items: z.infer<T>[] = [];

    if (text.trim() === "") return items;

    try {
        if (multipleObjects) {
            const candidates = text.split("}{");
            const { length } = candidates;
            candidates[0] += "}";
            candidates[length - 1] = "{" + candidates[length - 1];
            for (let i = 1; i < length - 1; i++) candidates[i] = `{${candidates[i]}}`;
            for (const candidate of candidates) items.push(JSON.parse(candidate));
        }
        else items.push(JSON.parse(text));
    }
    catch (e) {
        console.error("Error parsing json stream", e);
    }

    return items;
}