import { redirect, type NavigationTarget } from "@sveltejs/kit";
import { authorize } from "./api";

const encodedSlash = "%2F";
export const sanitizeRouteForUrlParam = (route: string) => route.replaceAll("/", encodedSlash);
export const restoreRouteFromUrlParam = (route: string) => route.replaceAll(encodedSlash, "/");

export const authorizeOrRedirect = async ({ route: { id } }: NavigationTarget) => {
    const authorized = await authorize();
    if (!authorized) redirect(307, id ? `/login?route=${sanitizeRouteForUrlParam(id)}` : "/login");
}